@import "../../scss/_variables.scss";
@import "../../scss/_foundation_media_queries.scss";

.content-panel {
    background-image: url("/img/walls/jalgaon.jpg");
    background-position: center;
    background-size: cover;
    padding: 4rem 1rem 8rem;
}

.alert-box.success {
    display: none;
    margin-top: 4rem;
}

.contact-us-form-panel {

    box-shadow: 0 0 20px 1px rgba(0, 0, 0, 0.5);
    background-color: rgba(255, 255, 255, 0.75);

    max-width: 1024px;

    min-height: 100px;
    margin: auto;
    padding-bottom: 2rem;
    position: relative;

}

.contact-us-form {
    margin-top: 4rem;
    padding: 0 1rem;

    // Overriding foundation rule
    .error label, .error label.error {
        color: $color-body-text;
    }

}

.contact-info-section {
    padding: 0 1rem;
    font-size: 1.125rem;
    position: relative;

    @media #{$medium-up} {
        margin-top: 4rem;
    }

}

.contact-us-input {
    font-weight: 400;
    transition-property: border-color, box-shadow !important;
}

.submit-button {
    display: block;
    height: 2.3125rem;
    padding: 0;
    letter-spacing: 1px;
}

.contact-info-labels {
    font-size: 1.3125rem;
    margin-top: 2rem;

    &:nth-of-type(1) {
        margin-top: 0;
    }

}

